<template>
    <div class="login-container">
        <el-form autoComplete="on" :model="loginData" ref="loginForm" label-position="left"
                 label-width="0px"
                 class="card-box login-form">
            <h3 class="title">系统登录</h3>
            <el-form-item prop="username">
                <span class="svg-container svg-container_login">
                  <icon-svg icon-class="yonghuming"/>
                </span>
                <el-input name="username" type="text" v-model="loginData.account" autoComplete="off" placeholder="邮箱"/>
            </el-form-item>
            <el-form-item prop="password">
                <span class="svg-container">
                  <icon-svg icon-class="mima"></icon-svg>
                </span>
                <el-input name="password" type="password" v-model="loginData.password"
                          autoComplete="off"
                          placeholder="密码"></el-input>
            </el-form-item>

            <el-form-item style="width: 70%">
                <span class="svg-container">
                      <icon-svg icon-class="mima"></icon-svg>
                 </span>

                <el-input
                        name="code"
                        style="width: 80%"
                        v-model="loginData.verificationCode"
                        autoComplete="off"
                        placeholder="验证码"
                        :maxlength="4"
                       ></el-input>
            </el-form-item>
            <img id="code" @click.stop="getCode"
                style="
                    margin-top:-9px;
                    position: absolute; 
                    right: 35px; top: 252px; border-radius: 5px;" width="88" height="47"
                :src="codeUrl + loginData.milisecond"/>
            <el-form-item>
                <span class="svg-container">
                  <icon-svg icon-class="mima"></icon-svg>
                </span>
                <el-input
                    name="googleCode"
                    v-model="loginData.googleCode"
                    autoComplete="off"
                    placeholder="谷歌验证码"
                    :maxlength="6"
                    @keyup.native.enter="confirmLogin"
                >

                </el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="confirmLogin">
                    登录
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {isvalidUsername} from '@/utils/validate'
    import fetch from '@/utils/fetch'
    import encodePassword from '@/utils/encode-password';
    import DCTP from '@/utils/dctp';

    export default {
        name: 'login',
        data() {
            return {
                loading: false,
                loginData: {
                    account: "",
                    password: "",
                    verificationCode: "",
                    milisecond: "",
                    googleCode: ''
                },
                // codeUrl: 'http://localhost:8090/validatecode/nofilter/gen?randomData='
                codeUrl: 'api/validatecode/nofilter/gen?randomData='
            }
        },
        methods: {
            confirmLogin() {
                let _this = this;

                if (this.validate()) {
                    let data = DCTP.utils.assign({}, this.loginData);
                    data.password = encodePassword.encrypt(this.loginData.password);
                    return new Promise((resolve, reject) => {
                        fetch({
                            url: 'admin/nofilter/login',
                            method: 'post',
                            data
                        }).then(function (response) {
                            if (response.result) {
                                _this.$store.commit('SET_TOKEN', response.data.token)
                                _this.$store.commit('SET_USER_ID', response.data.userId)
                                _this.$store.commit('SET_USER_INFO', response.data);
                                _this.$router.push({path: '/dashboard'});
                            } else {
                                _this.getMessage(response.msg, response.result);
                                _this.getCode();
                            }
                        })
                    })
                }
            },
            getCode() {
                this.loginData.verificationCode = '';
                this.loginData.milisecond = new Date().getTime();
            },
            getMessage(msg, seccess) {
                if (seccess) {
                    this.$message({
                        showClose: true,
                        message: '登录成功',
                        type: 'success'
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: msg,
                        type: 'error'
                    })
                }
            },
            validate() {
                if (!this.loginData.account) {
                    this.$message.warning('请输入登录账号');
                    return false;
                }
                if (!this.loginData.password) {
                    this.$message.warning('请输入登录密码');
                    return false;
                }
                if (!this.loginData.verificationCode) {
                    this.$message.warning('请输入验证码');
                    return false;
                } else if (!/^[0-9a-z]{4}$/i.test(this.loginData.verificationCode)) {
                    this.$message.error('验证码由4位数字或字母组成');
                    return false;
                }

                return true;
            }
        },
        created() {
            this.getCode();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "src/styles/mixin.scss";

    $bg: #2d3a4b;
    $dark_gray: #889aa4;
    $light_gray: #eee;

    .login-container {
        @include relative;
        height: 100vh;
        background-color: $bg;
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #fff !important;
        }
        input {
            background: transparent;
            border: 0px;
            -webkit-appearance: none;
            border-radius: 0px;
            color: $light_gray;
            height: 47px;
        }
        .el-input {
            display: inline-block;
            height: 47px;
            width: 85%;
        }
        .tips {
            font-size: 14px;
            color: #fff;
            margin-bottom: 10px;
        }
        .svg-container {
            padding: 6px 5px 6px 15px;
            color: $dark_gray;
            vertical-align: middle;
            width: 30px;
            display: inline-block;
            &_login {
                font-size: 20px;
            }
        }
        .title {
            font-size: 26px;
            font-weight: 400;
            color: $light_gray;
            margin: 0px auto 40px auto;
            text-align: center;
            font-weight: bold;
        }
        .login-form {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 400px;
            padding: 35px 35px 15px 35px;
            -webkit-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            -o-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
        }
        .el-form-item {
            border: 1px solid rgba(255, 255, 255, 0.1);
            background: rgba(0, 0, 0, 0.1);
            border-radius: 5px;
            color: #454545;
        }
        .show-pwd {
            position: absolute;
            right: 10px;
            top: 7px;
            font-size: 16px;
            color: $dark_gray;
            cursor: pointer;
        }
        .thirdparty-button {
            position: absolute;
            right: 35px;
            bottom: 28px;
        }
        .el-form-item {
            margin-bottom: 18px;
        }
    }
</style>



